const { __ } = wp.i18n
const { compose } = wp.compose;
const { select, withDispatch } = wp.data
const {  PanelBody, IconButton,TextControl, SelectControl, Tooltip, Button, RangeControl ,FormFileUpload} = wp.components
const { Component, Fragment, createRef } = wp.element
const { InspectorControls, MediaPlaceholder,MediaUpload, InnerBlocks, InspectorAdvancedControls } = wp.blockEditor
const { TestField,Background,Range, globalSettings: { globalSettingsPanel, animationSettings },globalCustomSettings:{globalCustomAttributes, HoverEXSettings},  HelperFunction: { videoBackground }, CssGenerator: { CssGenerator }, withCSSGenerator, InspectorTabs, InspectorTab } = wp.wprigComponents
import Modal from './../modal';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';
class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            device: 'md',
            openModal:false,
            imageCollection:[],
            imageUrl:"",
            openClass:""
        };
        this.wprigContextMenu = createRef();
    }
    componentDidMount() {
        const { setAttributes, clientId, attributes: { uniqueId , imageItems} } = this.props
        const { imageCollection } = this.state
        const { getBlockRootClientId } = select('core/block-editor');

        let parentClientId = getBlockRootClientId(clientId)

        if (parentClientId) {
            this.setState({ hideRowSettings: true })
        }

        const _client = clientId.substr(0, 6)
        if (!uniqueId) {
            setAttributes({ uniqueId: _client, childRow: parentClientId ? true : false });
        } else if (uniqueId && uniqueId != _client) {
            setAttributes({ uniqueId: _client, childRow: parentClientId ? true : false });
        }

       
            this.setState({
               imageCollection : imageItems
           })
       
           if(jQuery(this.el).find("#gallery")){
            // jQuery(this.el).find("#gallery").unitegallery().destroy()
            // jQuery(this.el).find("#gallery").unitegallery()
           }
        }
        
        componentWillUpdate(nextProps, nextState){
            const { setAttributes, clientId, attributes: { uniqueId , imageItems} } = this.props                     
            if(imageItems!= nextProps.attributes.imageItems){
                this.setState({
                    imageCollection : imageItems
                })

            }
    }
    openModal(el){
        console.log(el);

    }

    openOverlay(){

        this.setState({
            openClass:"open"
        });       

    }
    closeModal(){
        this.setState({
            openModal:false,
            imageUrl:"",
        });  
    }
    closeOverlay(){
        this.setState({
            openClass:""
        });   
        const t = this;
        setTimeout(function(){
            t.closeModal()
        },250)
    }

    renderClick(el){
        this.setState({
            openModal:true,
            imageUrl:el.url,
        });       
        const t = this;
        setTimeout(function(){
            t.openOverlay()
        },250)
    }
    renderCells(imageItems,enableHoverFx){
        console.log(imageItems);
        if(imageItems && imageItems.length>0){
            return imageItems.map((el)=>{       
                return(
                    <div class="cells">
                        {enableHoverFx && 
                            <div className="overlay">
                                <div className="overlay-content">
                                    <button type="button" onClick={()=>{this.renderClick(el)}}>View</button>
                                    <button type="button">Link</button>
                                </div>
                            </div>
                        }
                        <img src = {el.thumbnail} data-image={el.url}  onClick={()=>{this.renderClick(el)}}/>
                    </div>
                )                                                        
        })
        }
    }
    renderImages(imageItems){
        if(imageItems.length>0){

            return imageItems.map((el)=>{       
                console.log(el);
                return(
                    <a href={el.url}>
                        <img src = {el.url} data-image={el.url}     data-description="This is a Lemon Slice"/>
                    </a>
                )                                                                      
        })
        }
    }

    render() {
        const {
            attributes: {
                uniqueId,
                className,
                imageItems,
                columns,
                rowGap,
                columnGap,
                cellHeight,
                modalOverlayBg,
                overlayEffect,
                enableHoverFx,
                hoverEffect,
                hoverEffectDirection,
                modalLayout
            },
            setAttributes 
        }  = this.props;

        const { device,imageCollection } = this.state;

        return (
            <Fragment>
                <InspectorControls>
                    <InspectorTabs tabs={['style', 'advance']}>
                        <InspectorTab key={'style'}>
                            <PanelBody initialOpen={false} title={__('Grid Settings')}>
                                <TestField label={__('Upload Images')} multiple={true} type={['image']} value={imageItems} panel={true} onChange={value => setAttributes({ imageItems: value })} />

                                <Range
                                        label={__('Number of Columns')}
                                        value={columns || ''}
                                        onChange={val => setAttributes({ columns: val })}
                                        min={1}
                                        max={10}
                                    />

                                    <Range
                                        label={__('Row Gap')}
                                        min={0} max={100}
                                        value={rowGap}
                                        onChange={val => setAttributes({ rowGap: val })}
                                        unit={['px', 'em', '%','vw']}
                                        responsive
                                        device={this.state.device}
                                        onDeviceChange={value => this.setState({ device: value })}
                                    />

                                    <Range
                                        label={__('Column Gap')}
                                        min={0} max={100}
                                        value={columnGap}
                                        onChange={val => setAttributes({ columnGap: val })}
                                        unit={['px', 'em', '%','vw']}
                                        responsive
                                        device={this.state.device}
                                        onDeviceChange={value => this.setState({ device: value })}
                                    />

                                    <Range
                                        label={__('Height')}
                                        min={0} max={900}
                                        value={cellHeight}
                                        onChange={val => setAttributes({ cellHeight: val })}
                                        unit={['px', 'em','vh']}
                                        responsive
                                        device={this.state.device}
                                        onDeviceChange={value => this.setState({ device: value })}
                                    />

                            </PanelBody>
                            <PanelBody initialOpen={false} title={__('Lightbox Settings')}>
                          
                            <SelectControl
                                    label={__('Effect')}
                                    value={overlayEffect}
                                    options={[
                                        { label: 'Fade in Scale', value: 'fade-in-scale' },
                                        { label: 'Newspaper', value: 'news-paper' },
                                        { label: 'Fall', value: 'fall' },
                                        { label: 'Side Fall', value: 'side-fall' },
                                        { label: 'Sticky Up', value: 'sticky-up' },
                                        { label: '3D Flip Horizontal', value: 'flip-horizontal' },
                                        { label: '3D Flip Vertical', value: 'flip-vertical' },
                                        { label: '3D Sign', value: 'sign' },
                                        { label: 'Super Scaled', value: 'super-scaled' },
                                        { label: 'Just ME', value: 'just-me' },
                                        { label: '3D Split', value: 'split' },
                                        { label: '3D Rotate Bottom', value: 'rotate-bottom' },
                                        { label: '3D Rotate In Left Bottom', value: 'rotate-in-left' },
                                        { label: 'Slip From Top', value: 'slip-from-top' },
                                        { label: 'Slide Down', value: 'overlay-slidedown' },
                                        { label: 'Slide Up', value: 'overlay-slideup' },
                                        { label: 'Slide Left', value: 'overlay-slideleft' },
                                        { label: 'Slide Right', value: 'overlay-slideright' },
                                    ]}
                                    onChange={val => setAttributes({ overlayEffect: val })}
                                />
                                <SelectControl
                                    label={__('Layout')}
                                    value={modalLayout}
                                    options={[
                                        { label: 'Layout 1', value: 'modal-layout-1' },
                                        { label: 'Layout 2', value: 'modal-layout-2' },
                                    ]}
                                    onChange={val => setAttributes({ modalLayout: val })}
                                />
                                <Background
                                            parallax
                                            value={modalOverlayBg}
                                            label={__('Background')}
                                            externalImage
                                            sources={['image', 'gradient', 'video']}
                                            onChange={val => setAttributes({ modalOverlayBg: val })}
                                        />
                            </PanelBody>
                        </InspectorTab>
                        <InspectorTab key={'advance'}>
                            {HoverEXSettings(uniqueId,enableHoverFx, hoverEffect,hoverEffectDirection, setAttributes)}  
                            {/* {animationSettings(uniqueId, animation, setAttributes)}  */}
                            {/* {interactionSettings(uniqueId, interaction, setAttributes)} */}
                        </InspectorTab>
                    </InspectorTabs>
                </InspectorControls>

              

            { this.state.openModal && 
                <Modal
                title="This is my modal"
                className={`wprig-dynamic-modal wprig-block-${uniqueId}${className ? ` ${className}` : ''}`}
                overlayClassName = {`wprig-block-${uniqueId} ${overlayEffect} ${this.state.openClass} `}  
				onRequestClose={ () => {this.closeOverlay() }}>
                    <img src={`${this.state.imageUrl}`}/>
                </Modal>
            }
            {imageItems.length==0 ? 
                <div className={`wprig-grids-editor wprig-grid-gallery wprig-block-${uniqueId}`} >
                   
                   <MediaPlaceholder
                        onSelect={(newImages) =>{
                            const newImgs = newImages.map((img) => {
                                return {
                                    url: img.sizes.full.url,
                                    thumbnail:img.sizes.medium.url,
                                    title:img.caption,

                                }
                            })
							setAttributes({
								imageItems: newImgs,								
                            })
                        }
                           
						}
						labels={{ title: "Image Slider" }}
						allowedTypes={["image"]}
						multiple
					/>
                    <MediaUpload
								value={imageItems.map((img) => img.id)}
								allowedTypes={["image"]}
								multiple
                                gallery
                                render={({ open }) => (
									<IconButton
										
										onClick={open}
										label={__("Edit selection")}
									/>
								)}
								onSelect={(newImages) => {
									const newImgs = newImages.map((img) =>
										captionArray.find((c) => c.id === img.id)
											? captionArray.find((c) => c.id === img.id)
											: {
													text: img.caption,
													url: img.url,
													thumbnail: img.thumbnail,
													id: img.id,
											  }
									);

									setAttributes({
										imageItems: newImgs,
										// descriptions: newCaptionArray,
									});
								}}
							/>
                </div> :
                <Fragment>
                    <Modal
                title={`${this.state.caption ? this.state.caption : "No Title" }  `}
                className={`wprig-dynamic-modal wprig-block-${uniqueId}${className ? ` ${className}` : ''} ${overlayEffect} ${this.state.openClass}` }
                overlayClassName = {`wprig-block-${uniqueId}`}  
				onRequestClose={ () => {this.closeOverlay() }}>
                    {modalLayout=='modal-layout-1' &&
                        <Fragment>
                            <img src={`${this.state.imageUrl}`}/>
                            <p>{this.state.description ? this.state.description : "No Description" }</p>
                        </Fragment>
                    }
                    {modalLayout=='modal-layout-2' &&
                        <Fragment>
                            <p>{this.state.description ? this.state.description : "No Description" }</p>
                            <img src={`${this.state.imageUrl}`}/>
                        </Fragment>
                    }
                </Modal>
                <div className={`wprig-grids-editor wprig-grid-gallery wprig-block-${uniqueId} ${enableHoverFx ? hoverEffect+' '+hoverEffectDirection : ' '} `} >
                    {this.renderCells(imageItems,enableHoverFx)}
                </div>
                </Fragment>
            }

                
            </Fragment>
        )
    }
}

export default withCSSGenerator()(Edit);