const { Component, Fragment, createRef } = wp.element
import Modal from './../modal';
import 'jquery-mosaic/jquery.mosaic.min.js';



class Mosaic extends Component {
    constructor(props) {
        super(props);
        this.state = {
            device: 'md',
            openModal:false,
            imageCollection:[],
            caption :"",
            imageUrl:"",
            openClass:""
        };
        this.wprigContextMenu = createRef();
    }
    renderClick(el){
        console.log(el);
        this.setState({
            openModal:true,
            imageUrl:el.url,
        });       
        const t = this;
        setTimeout(function(){
            t.openOverlay()
        },250)
    }

    openOverlay(){

        this.setState({
            openClass:"open"
        });       

    }

    closeModal(){
        this.setState({
            openModal:false,
            imageUrl:"",
        });  
    }
    closeOverlay(){
        this.setState({
            openClass:""
        });   
        const t = this;
        setTimeout(function(){
            t.closeModal()
        },250)
    }

    componentDidMount() {
        const {
            className,
            id,
            images  
        }  = this.props;

        console.log(id);
        var the_id = id;

        if(jQuery("."+id).find("#gallery") ){
            // setTimeout(function(){
            //     jQuery("."+the_id).find("#gallery").unitegallery()
            //     console.log("Hello World"+the_id);

            // },5000)
            const loadImage = image => {
      return new Promise((resolve, reject) => {
        const loadImg = new Image()
        loadImg.src = image.url
        // wait 2 seconds to simulate loading time
        loadImg.onload = () =>
          setTimeout(() => {
            resolve(image.url)
          }, 2000)

        loadImg.onerror = err => reject(err)
      })
    }

            Promise.all(images.map(image => loadImage(image))).then(() => {
                // $("."+id).find("#gallery").unitegallery()
            console.log("All Images are loaded");
            
            this.setState({doneLoading:true})
            setTimeout(function(){
                jQuery("#gallery").Mosaic({maxRowHeight:400});
            },500)
            }).catch(err => console.log("Failed to load images", err))

        }
    }

    renderCells(imageItems,enableHoverFx){
        console.log(imageItems);
        if(imageItems && imageItems.length>0){
            return imageItems.map((el)=>{       
                return(
                    <div class="cells">
                        {enableHoverFx && 
                            <div className="overlay">
                                <div className="overlay-content">
                                    <button type="button" onClick={()=>{this.renderClick(el)}}>View</button>
                                    <button type="button">Link</button>
                                </div>
                            </div>
                        }
                        <img 
                        src = {el.url} 
                        data-image={el.url}  
                        onClick={()=>{ !enableHoverFx ? this.renderClick(el): ''}}
                        />
                        
                    </div>
                )                                                        
        })
        }
    }

    render() {
        const {
            className,
            id,
            overlayEffect,
            enableHoverFx,
            hoverEffect,
            modalLayout,
            hoverEffectDirection,
            images  
        }  = this.props;

        const { doneLoading } = this.state;

       

        if(!doneLoading){
            return(
                <Fragment>
                    <p>Gallery is loading</p>
                </Fragment>
            )
        }

        return(
            
            <Fragment>
                { this.state.openModal && 
                <Modal
                title={`${this.state.caption ? this.state.caption : "No Title" }  `}
                className={`wprig-dynamic-modal wprig-block-${id}${className ? ` ${className}` : ''} ${overlayEffect} ${this.state.openClass}` }
                overlayClassName = {`wprig-block-${id}`}  
				onRequestClose={ () => {this.closeOverlay() }}>
                    {modalLayout=='modal-layout-1' &&
                        <Fragment>
                            <img src={`${this.state.imageUrl}`}/>
                            <p>{this.state.description ? this.state.description : "No Description" }</p>
                        </Fragment>
                    }
                    {modalLayout=='modal-layout-2' &&
                        <Fragment>
                            <p>{this.state.description ? this.state.description : "No Description" }</p>
                            <img src={`${this.state.imageUrl}`}/>
                        </Fragment>
                    }
                </Modal>
            }
                <div class={`${className}  ${id} ${enableHoverFx ? hoverEffect+' '+hoverEffectDirection : ' '} `}>
                    <div id="gallery">
                        {this.renderCells(images,enableHoverFx)}
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default Mosaic;